@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: 'key-pair-dialog.html',
  styleUrls: ['./create-wallet.component.css']
})
export class DialogOverviewNetworkDialog {

  address: string = '';

  getAddress() {
    let add = JSON.parse(sessionStorage.getItem('wallet'));
    if (add['address']) {
      this.address = add['address'];
    }
  }

  constructor(
    public dialogRef: MatDialogRef<DialogOverviewNetworkDialog>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData,
    private router: Router,
    private apiServices: ApiServices
  ) { }

  ngOnInit() {
    this.getAddress();
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  accessWallet() {
    this.onNoClick();
    this.apiServices.importWifSession();
    // this.router.navigate(['/access-wallet']);
  }
}